<template>
<div>
  <el-container>
    <el-aside width="200px" class="recordsbox">
      <div style="font-weight: bold">Dialogue records</div>
    <div class="aside_wrap">
      <div v-for="(itemHis,index) in historyMessage" :key="index">
        {{itemHis.question}}
      </div>
    </div>
    </el-aside>
    <el-main style="">
      <div class="main_1">
        <div class="main_2" >
          <div  class="main_3" >
            <div class="main_4" ref="scrollContainer">
              <div class="main_5" >
                <div class="resultsbox">
                  <div v-for="(itemHis,index) in historyMessage" :key="index">
                    <div class="questionBox">{{itemHis.question}}</div>
                    <div class="resultTextBox">
                      <el-collapse accordion>
                        <el-collapse-item>
                          <template slot="title">
                            Reference Documents
                          </template>
                          <div v-for="(item,index) in itemHis.response.myDocuments" :key="index">
                            <div @click="showDocumentDetail(item)" class="itemBox" style="cursor: pointer;" >{{index+1+" "}}{{item.title}}</div>
                          </div>
                        </el-collapse-item>
                      </el-collapse>
                      <div class="resBox" v-html="compileMarkdown(itemHis.response.text)"></div>
                    </div>
                    </div>


                </div>
              </div>

            </div>

          </div>
        </div>
        <div style="position: absolute;flex-direction: row;justify-content: center;width: 100%;z-index: 10;display: flex;bottom: 20px">
          <div style="max-width: 90%;padding:2px 82px 0;width: 100%">
            <el-card class="box-card" :class="{'highlighted': isInputFocused}">
              <div class="container_text">
<!--                <input type="text" ref="inputdata" @keyup.enter="handleEnter"/>-->
                <el-input placeholder="Please input the text." v-model="text" class="no-border-input"  @focus="handleFocus" @keyup.enter.native="handleEnter"
                          @blur="handleBlur"></el-input>
              </div>
                <el-button ref="subButton" icon="el-icon-s-promotion" round class="submit_btn" @click="handleSubmit1" ></el-button>


            </el-card>
          </div>
        </div>
      </div>

    </el-main>
  </el-container>
  <el-dialog
    title=""
    :visible.sync="centerDialogVisible"
    width="60%"
    center>
    <el-table
      :data="documentTable"
      height="300px"
      :header-cell-style="{lineHight:'0',height: '0px',padding:'0'}"
      :cell-style="cellStyle"
      :row-style="{height:'20px'}"
      style="width: 100%">
      <el-table-column
        prop="key"
        label=""
        class-name="tableHeight"
        width="100">
      </el-table-column>
      <el-table-column
        prop="value"
        label=""
        class-name="tableHeight"
        width="">
        <template slot-scope="scope">
          <span style="margin-left: 10px">{{ scope.row.value }}</span>
        </template>
      </el-table-column>
    </el-table>
    <span slot="footer" class="dialog-footer">
    <el-button @click="centerDialogVisible = false">cancel</el-button>

  </span>
  </el-dialog>
</div>
</template>

<script>
  import {marked} from 'marked';
  export default {
    data() {
      return {
        text:"",
        resultText:"",
        myDocuments:[],
        centerDialogVisible:false,
        documentTable:{
        },
        isInputFocused:false,
        ifShowDoc:false,
        historyMessage:[],
        count:0,
        count1:0,



      }
    },

    mounted() {

    },
    methods: {
      compileMarkdown(text) {
        // 使用 marked 库将 Markdown 文本转换为 HTML
        return marked(text);
      },
      handleSubmit1() {
        if (this.text.trim() === "") {
          return
        }
        let data = {
          text: this.text
        }

        this.$http.post('/chat2/', data).then((res) => {
          let resultData = res.data.data
          let resultText = resultData.text

          // this.documents = resultData.document
          let Documents_list=[]
          let myDocument = resultData.document
          for (let i=0;i<myDocument.length;i++){
            let tableData=[]
            let title = ""
            for(let key=0;key<myDocument[i].keys.length;key++){
              let obj={
                key:'',
                value:''
              }
              obj.key=myDocument[i].keys[key]
              obj.value=myDocument[i].values[key]
              tableData.push(obj)
              if (obj.key == 'title'){
                title=obj.value
              }
            }

            let document_tem={
              title:title,
              tableDate:tableData
            }
            Documents_list.push(document_tem)
          }
          let historyTemp = {
            question: this.text,
            response: {'text':resultText,'myDocuments':Documents_list}
          }
          this.historyMessage.push(historyTemp)
          this.$nextTick(() => {
            this.scrollToBottom();
          });
          this.text = ""
        })

      },
      showDocumentDetail(item) {
        this.documentTable = item.tableDate
        if (!this.centerDialogVisible) {
          this.centerDialogVisible = true
        }
      },
      handleFocus() {
        this.isInputFocused = true;
      },
      // this.$refs.inputdata.focus(
      handleBlur() {
        this.isInputFocused = false;
      },
      handleEnter(e) {
        this.handleSubmit1();
      },
      scrollToBottom() {
        const container = this.$refs.scrollContainer;
        container.scrollTop = container.scrollHeight;
      },
      cellStyle({ row, column, rowIndex, columnIndex }) {
    //第二列样式默认居左，其他列居中
    if (columnIndex === 1) {

    } else {
      return { 'background-color': '#e6e6e6' ,'font-weight':'bold'}
    }
  }
    }
  }
</script>

<style scoped>
  /deep/ .el-main{
    position: relative;height: 100vh;box-sizing: border-box;display: flex;margin-top:0;padding: 0
  }
 .main_1{
 position: relative;background-color: rgb(231,236,247);display: flex;justify-content: center;overflow: hidden;width: 100%
